import Archives from '@/assets/archives.json';

export default {
  namespaced: true,
  state: {
    blogList: Blogs,
    archives: Archives,
  },
  mutations: {
  },
  actions: {
  },
  getters: {
    getBlogList: state => state.blogList,
    getArchives: state => state.archives,
  },
};



