<template>
  <div class="main">
    <router-view></router-view>
    <footerSide></footerSide>
  </div>
</template>

<script>
import footerSide from '@/components/layout/footer';

export default {
  name: 'App',
  components: {
    footerSide,
  },
  created() {
    this.$nprogress.start();

    this.$router.beforeEach((to, from, next) => {
      this.$nprogress.start();
      next();
    });

    this.$router.afterEach((to, from) => {
      this.$nprogress.done();
    });
  },
};
</script>

<style>
body {
  background: #dedede;
}

label {
  margin-left: 0 !important;
}

.main {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

.header {
  padding: 15px 0 5px 0;
}

#nprogress .bar {
}
</style>



