import Vue from 'vue';
import Router from 'vue-router';

// Blogs List
import Blogs from '@/assets/blogs.json';

const Index = () => import('@/components/');
const Main = () => import('@/components/layout/main/');
const About = () => import('@/components/about/');
const Archives = () => import('@/components/archives/');
const Blog = () => import('@/components/blog/');

Vue.use(Router);

const cvtBlogs = (BlogsList, BlogComponent) => {
  const rewBlogs = [];

  BlogsList.forEach((blog) => {
    const blogPath = `/${blog.date}/${blog.subtitle}`;

    rewBlogs.push({
      path: blogPath,
      component: BlogComponent,
      meta: {
        blogPath,
      },
    });
  });

  return rewBlogs;
};

export default new Router({
  mode: 'history',
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition;
    }
    return { x: 0, y: 0 };
  },
  routes: [
    {
      path: '/',
      component: Index,
      children: [
        { path: '/', name: 'IndexMain', component: Main },
        { path: '/about', component: About },
        { path: '/archives', component: Archives },
        ...cvtBlogs(Blogs, Blog),
      ],
      meta: {
        showProgressBar: false,
      },
    },
  ],
});



