import * as nprogress from 'nprogress';

const commonFunc = {};

commonFunc.install = (Vue) => {
  const vue = Vue;
  vue.prototype.$common = {
    fadeInEffect(target) {
      const fadeTarget = target;
      const fadeEffect = setInterval(() => {
        if (!fadeTarget.style.opacity) fadeTarget.style.opacity = 0;

        const opaVal = parseFloat(fadeTarget.style.opacity) + 0.3;
        if (!(opaVal > 1)) fadeTarget.style.opacity = opaVal;
        else clearInterval(fadeEffect);
      }, 200);
    },
    fadeOutEffect(target) {
      const fadeTarget = target;
      const fadeEffect = setInterval(() => {
        if (!fadeTarget.style.opacity) fadeTarget.style.opacity = 1;
        if (fadeTarget.style.opacity > 0) fadeTarget.style.opacity -= 0.1;
        else clearInterval(fadeEffect);
      }, 200);
    },

    findLabel($const, label) {
      const keys = Object.keys($const);
      const values = Object.values($const);
      for (let i = 0; i <= keys.length; i += 1) {
        if (keys[i] === label) return values[i];
      }

      return 'black';
    },
  };

  vue.prototype.$nprogress = {
    start() {
      nprogress.start();
    },
    set(pef) {
      nprogress.set(pef);
    },
    done() {
      nprogress.done();
    },
  };
};

export default commonFunc;



